    insertInputTxt(id,value,value1) {
      var elInput = document.getElementById(id);   
      var startPos = elInput.selectionStart;  
      var endPos = elInput.selectionEnd;
      for(let i in this.customTemplateFormBot) {
        for(let j in this.customTemplateFormBot[i]) {
          if(j == value1) {
            this.customTemplateFormBot[i][j] = this.customTemplateFormBot[i][j].substring(0, startPos) + value + this.customTemplateFormBot[i][j].substring(endPos)
          }
        }
      }     
