    onDragStart(event){
         // console.log("drag start")
          this.draging=event.target;
        this.dragindex1=this._index(this.draging);
    },
     onDragOver(event){
            // console.log('drag move')
            this.target=event.target;
             if (this.target.nodeName === "LI" && this.target !== this.draging) {
                 this.fileList[this._index(this.draging)] = this.fileList.splice(this._index(this.target), 1, this.fileList[this._index(this.draging)])[0];
       
            }
    
     },
     
     onDragEnd(event){
        // console.log('drag end');
            let currentNodes=Array.from(this.$refs.parentNode.childNodes);
  
              let data=currentNodes.map((i,index)=>{
                      let item=this.fileList.find(c=>c==i.innerText);
                      return item
                });
          //  console.log(this.fileList)
     },
     _index(el){
      let domData=Array.from(this.$refs.parentNode.childNodes);
      return domData.findIndex(i=>i.innerText==el.innerText);
      },
    setMainPic(index){
       this.fileList.unshift( this.fileList.splice(index , 1)[0]);
    },
     // 移除效果图
    removeResultPic(index) {
      this.fileList.splice(index,1)  
    },
    // 插入效果图
    confirmInserVar(item) {
      if(item == '全部') {
        this.fileList = []
        for(var i = 1;i< this.resultWordList.length;i++) {
          var obj1 = {
            index: this.resultWordList[i].name,
            type: '1',
            url:''
          }
          this.fileList.push(obj1)
        }   
      }else {
         var obj = {
           index: item,
           type: '1',
           url:''
         }
         this.fileList.push(obj)
      }
    },
    handleSizeChange(val) {
      this.limit = val
      this.getTableList();
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getTableList();
