    // 删除图片
    handleRemove(file) { 
    },
    handleChange(file, fileList) {
      this.imageList.push(file);
      this.fileList = fileList;
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    // 编辑模板信息
    handleEdit(data) {
      this.$router.push({ path: "/editTemplate", query: { id: data.id } });
    },
    async copyTemplate(data) {
      let res = await this.api.copyExcel({id: data.id})
      if (res.data.code == "200") {
        this.$message({
          message: "复制模板成功",
          type: "success"
        });
        this.getTableList();
      } else {
        this.$message.error("复制模板失败");
      }
    },
    // 删除模板信息（删除前处理）
    beforeDelete(data) {
      this.$confirm("此操作将永久删除该模板信息, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.handleDelete(data.id);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    // 删除模板信息
    async handleDelete(id) {
      let res = await this.api.templateDelete({id: id})
      if (res.data.code == "200") {
        this.$message({
          message: "删除模板成功",
          type: "success"
        });
        this.getTableList();
      } else {
        this.$message.error("删除模板失败");
      }
    },
    // 显示导入自定义对话框
    isBlock(data, template_name,path,productTypeList) {
      this.bigObj = data
      this.saveDisabled = false
      this.customTemplateForm.template_name = template_name;
      this.dialogImportTemVisible = true;
      this.productTypeList = productTypeList
      this.customTemplateForm.product_type = this.productTypeList[0]
      this.productVaribles = data[this.productTypeList[0]];
      this.customTemplateForm.excel_message = data[this.productTypeList[0]];
      this.customTemplateForm.path = path
      this.$set(this.customTemplateForm,'productTypeList',productTypeList)
      this.$set(this.customTemplateForm,'all_excel_message',data)
      var varLength = this.productVaribles.length;
      for (var i = 0; i < this.productVaribles.length; i++) {
        var iLength = this.productVaribles[i].length;
        for (var j = 0; j < iLength; j++) {     
          if (j == 0) {
            this.$set(
              this.customTemplateFormBot,
              this.productVaribles[i][0].name,
              {}
            );
          } else {
            if (this.productVaribles[i][j].params) {
              this.productVaribles[i][j].params.unshift('无')
               if(this.productVaribles[i][j].name == "parent_child") {
                 this.$set(
                    this.customTemplateFormBot[this.productVaribles[i][0].name],
                    this.productVaribles[i][j].name,
                    'Child'
                 )
               }else if(this.productVaribles[i][j].name == "relationship_type") {
                  this.$set(
                    this.customTemplateFormBot[this.productVaribles[i][0].name],
                    this.productVaribles[i][j].name,
                    'Variation'
                 )
                }else {
                 this.$set(
                    this.customTemplateFormBot[this.productVaribles[i][0].name],
                    this.productVaribles[i][j].name,
                    ''
                 );
               }
              
            } else {
              this.$set(
                this.customTemplateFormBot[this.productVaribles[i][0].name],
                this.productVaribles[i][j].name,
                ""
              );
            }
          }
        }     
      }
      for(let key in data) {
        for(var s =0;s<data[key].length;s++) {
          for( var k=1;k<data[key][s].length;k++) {
            this.$set(data[key][s][k],'values','')
          } 
        }
        if(key!==this.productTypeList[0]) {
          for(var j=0;j<data[key].length;j++) {
            for( var k=1;k<data[key][j].length;k++) {
              if(data[key][j][k].params) {
                data[key][j][k].params.unshift('无')
              }
            } 
          }
        }
      }
      console.log(this.productVaribles);
      
    },
    // 获取国家列表
    async getCountryList() {
      let res = await this.api.CountryList()
      if (res.data.code == "200") {
        this.countryList = res.data.data;
      }else {
        this.errorMess('获取国家列表失败')
      }
    },
    imgUpload() {
      if(this.imageList.length ==0) {
        this.$message({
              message: "请上传产品图片",
              type: "error"
            });
      }else {
      this.saveDisabled = true
      //自定义的上传图片的方法
      // 1. 创建formData 利用AXIOS传递
      let formData = new FormData();
      let _this = this;
      formData.append("files", this.files);
      for (let index = 0; index < _this.imageList.length; index++) {
        const element = _this.imageList[index].raw;
        formData.append("image[]", element);
      }
      this.imgRequest("/v1/upLoadimgs", formData)
        .then(res => {
          if(res.data.code == 200) {
            this.customTemplateForm.imgs = res.data.data;
             this.submitInfo();
          }  
        })
        .catch(error => {
          this.saveDisabled = false
          });
      }

    },
    // 点击确定  提交模板信息
    async submitInfo() {
      console.log(this.fileList);
      for(let i in this.customTemplateFormBot) {
        if(i!=='Required') {
          for(let item in this.customTemplateFormBot[i]) {
            if(this.customTemplateFormBot[i][item]=='无') {
              this.customTemplateFormBot[i][item] =''
            }
          }
        }
      }
      for(let i in this.customTemplateFormBot.Required) {
        for(var j =0;j<this.customTemplateForm.excel_message[0].length;j++) {
          if(i == this.customTemplateForm.excel_message[0][j].name) {
            if(this.customTemplateForm.excel_message[0][j].type=="Required") {
              if(this.customTemplateFormBot.Required[i] == '') {
                this.$message({
                  type: "error",
                  offset: 300,
                  message: i +'不能为空'
                });
                return
              }
              if(this.customTemplateFormBot.Required[i] == '无') {
                this.$message({
                  type: "error",
                  offset: 300,
                  message: i +'不能为空'
                });
                return
              }
            }
          }
        }
        
      }
      this.confirmExportLoading = true
      for (var i = 0; i < this.countryList.length; i++) {
        if (
          this.countryList[i].zone_name ==
          this.customTemplateForm.template_country
        ) {
          this.customTemplateForm.template_country_id = this.countryList[i].id;
        }
      }
      
      
      this.customTemplateForm.data.push(this.customTemplateFormBot);
      for(var i=0;i<this.fileList.length;i++) {
        var obj = {}
        if(i == 0) {
          if(this.fileList[0].type == '1') {
            this.$set(
             obj,
             'main_image_url',
             this.fileList[0].index
           )
           this.$set(
             obj,
             'type',
             this.fileList[0].type
           )
          }else {
            this.$set(
             obj,
             'main_image_url',
             this.fileList[0].url
           )
           this.$set(
             obj,
             'type',
             this.fileList[0].type
           )
          }
           
        }else {
          if(this.fileList[i].type == '1') {
            this.$set(
              obj,
              'other_image_url'+i,
              this.fileList[i].index
            )
            this.$set(
             obj,
             'type',
             this.fileList[i].type
           )
          }else {
            this.$set(
              obj,
              'other_image_url'+i,
              this.fileList[i].url
            )
            this.$set(
             obj,
             'type',
             this.fileList[i].type
           )
          }
          
        }
        this.customTemplateForm.imgs.push(obj)
      }
      if(this.customTemplateForm.sales_type!=='产品变体') {
        this.customTemplateForm.colorNameRegular = ''
        this.customTemplateForm.concatNumber = ''
      }
      let u_id = sessionStorage.getItem('selectUId')
      let res = await this.api.templateAdd(this.customTemplateForm)
      if (res.data.code == "200") {
        this.confirmExportLoading = false
        this.dialogImportTemVisible = false;
        this.$message({
          message: "添加成功",
          type: "success"
        });
        this.getTableList();
      }else {
        this.errorMess('添加失败')
      }
    },
    searchtemplate(){
      this.getTableList();
    },
     resettemplate(){
       this.templateName="";
        this.createdTime="";
         this.endedTime="";
      this.getTableList();
    },
    // 获取店铺列表
    async getStoreList() {
      const res = await this.api.stoLists()
      if(res.data.code == 200) {
        this.storeList = res.data.data
        this.customTemplateForm.store_name = this.storeList[0].id
      }
    },
    // 获取列表
    async getTableList() {
      this.templateLoading = true
      let u_id = sessionStorage.getItem('userId')
      let res = await this.api.templateIndex({page:this.currentPage,limit:this.limit,store_id:this.checkboxshop,template_name:this.templateName,createdTime:this.createdTime,endedTime:this.endedTime});  
      if (res.data.code == "200") {
        this.tableData = res.data.data.data;
        this.total = res.data.data.total;
        this.pageSize = res.data.data.per_page;
        this.templateLoading = false
      }
    }
  },
  created() {
    this.getCountryList();
    this.getTableList();
    this.getStoreList()
  },
  computed: {
    length() {
      return this.fileList.length;
    }
  },
  watch : {
    'customTemplateForm.product_type':function(val){
      this.productVaribles = this.bigObj[val]
      this.customTemplateForm.excel_message = this.bigObj[val]
    }
  },
  components: {
    uploadExcel
  }
};
